@import url(<link href="https://fonts.googleapis.com/css?family=Josefin+Sans:300,400,600,700&display=swap" rel="stylesheet">);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html{
  margin:0;
  padding:0;
  font-family: 'Josefin Sans', sans-serif;
  background: rgb(0, 25, 25);
}

*{
 box-sizing: border-box;
}

main{
  padding-top: 20vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

button, .button{
    background-color: rgb(0, 25, 25);
    cursor: pointer;
    color: whitesmoke;
    min-width: 6rem;
    padding: .6rem;
    display: block;
    border: none;
    margin-bottom: 10px;
    font-size: .8rem;
    box-shadow:  2px 2px 7px rgb(0, 34, 34);
}

.button{
  border: none;
  padding: .4rem;
  padding: .4rem;
  max-width: 6rem;
  text-align: center;
  font-size: 1rem;
  
}

a{
  text-decoration: none;
  color: rgb(255, 162, 36);
  cursor: pointer;
}

div {
  color: rgb(255, 162, 36);
}



.project{
    position: relative;
    display:grid;
    grid-template-columns:1fr 1fr;
    height:70vh;
}
.project-content {
    -webkit-transition: 6s ease;
    transition: 6s ease;
    background: rgb(0, 25, 25);
    color: whitesmoke;
    position:relative;
}

.project-content .abs{
    position:absolute;
    top:50px;
    right:-50px;
}
.project:nth-of-type(even) .project-content .abs{
    top:50px;
    left:-50px;
    right:auto;
}


.project img {
    width: 100%;
    height:100%;
    object-fit: cover;
    object-position: center top;
    box-shadow: 0 8px 6px -6px black;
}
.project:nth-of-type(odd) .project-content{
    grid-row-start: 1;
}


.admin-icons{
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    display: grid;
    grid-auto-flow: column;
    justify-content: end;
    grid-gap:1rem;
    gap:1rem;
}

.edit-icons{
    color: rgb(105, 105, 105);
}

svg{
   color: rgb(255, 162, 36);
   padding-left: .2rem;
   size: 4rem;
}

@-webkit-keyframes zoomIn{
    from{
        -webkit-transform:scale(0.0);
                transform:scale(0.0);
    }
    to{
        -webkit-transform: scale(100vw, 100vh);
                transform: scale(100vw, 100vh);
    }
}

@keyframes zoomIn{
    from{
        -webkit-transform:scale(0.0);
                transform:scale(0.0);
    }
    to{
        -webkit-transform: scale(100vw, 100vh);
                transform: scale(100vw, 100vh);
    }
}

@media (max-width:1000px){
    .project{
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 2fr;
    }
    .project-content #intro-project{
        font-size: 2rem;
        text-align: center;
        display: grid;
    }

}
.projects-container{
  display: grid;
  grid-gap:5rem;
  gap:5rem;
  padding:0 1rem 1rem 1rem;
  background: rgb(0, 25, 25);
}

.add{
  position: fixed;
  width: 100vw;
  left: 0;
  top: 10vh;
  height: 5vh;
  background-color: rgb(0, 25, 25);
  display: grid;
  place-items: center;
}   


main.project-detail{
    padding:0;
    justify-content: start;
    align-items:start;
}

.parallax-overlay{
    background:rgba(0,0,0,.2);
    width:100vw;
    height:100vh;
    display:grid;
    place-items:center;
    color:rgb(0, 0, 0);
    font-size:2rem;
}

.project-detail .project-content{
    min-height:100vh;
}

.project-content > div, h1{
    color: rgb(255, 162, 36);
    place-items: center;
    justify-content: center;
    font-size: 4rem;
}

.project-detail .project-images{
    width:100%;
    display:grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap:.5rem;
    gap:.5rem;
}

.project-images img{
    width:100%;
    height:200px;
    object-fit: cover;
}

.pager{
    display:grid;
    grid-auto-flow: column;
    align-items: center;
    justify-content: center;
    grid-gap:4rem;
    gap:4rem;
}

.project .description{
    text-align:left;
}

p, h2{
    font-size: 1.2rem;
    grid-template-columns: 1fr 2fr;
    display: grid;
    margin-left: 10%;
    line-height: 1.5rem;

}

.langfil img{
    width: 100%;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr;

}

*{
    color: rgb(255, 162, 36);
}
.lj p{
    font-size: 5rem;
    line-height: 90px;
    display: grid;
    grid-template-columns:1fr 1fr;

}

 
.header-container{
    position: fixed;
    width: 100vw;
    height: 15vh;
    background: (0, 25, 25);
    color: rgb(250, 250, 250);
    display: grid;
    place-items: center;
    z-index: 2;
}

header {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: rgb(0, 25, 25);
    display: grid;
    grid-auto-flow: column;
    justify-content: left;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    z-index: 2;
    padding: 2rem;
}

.burger{
    display: none;
    z-index: 2;
    position: absolute;
    cursor: pointer;
}

header a{
    text-decoration: none;
    font-weight: 400;
    color: rgba(255, 162, 36, 0.301);
}

[aria-current='page'], .active {
    font-weight: 700;
    color: rgb(255, 162, 36);
}

header img{
    width: 9%;
    z-index: 1;
    cursor: pointer;
}

img{
    padding: 0;
}

@media (max-width:1000px){
    header{
        width: 100%;
        -webkit-transform:translateY(-100%);
                transform:translateY(-100%);
        grid-auto-flow: row;
        align-content: center;
        min-height: 100vh;
        -webkit-transition: .4s ease;
        transition: .4s ease;
    }
    header a{
        font-size: 3rem;
        color: whitesmoke;
    }

    header.visible{
        background-color: rgb(153, 153, 153);
        -webkit-transform:translateY(0);
                transform:translateY(0);
    }
    .burger{
        display: block;
    }
    main{
        padding: 30vh 5vh;
    }

}
main.login{
    align-items: left;
}

.login img {
    width: 200px;
    margin-bottom: 1rem;
    box-shadow:  2px 2px 7px rgb(0, 34, 34);
    border-radius: 50%;
}
input, textarea, label, select, button, .button{
    font-size: 1rem;
    padding: .5rem;
    display: block;
    margin-bottom: 1rem;
    color: rgb(0, 0, 0);
    margin:1rem;
  }
  .edit-icons {
    -webkit-transform:scale(1.5);
            transform:scale(1.5);
  }
  
  .edit-icons{
    color: rgb(199, 199, 199);
    cursor: pointer;
    z-index: 1;
  }

  .project-images{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-gap: .2rem;
    gap: .2rem;
    margin-bottom: 1rem;
    justify-content: left;
    margin:2rem 0 2rem 0;
  }

  .project-images > div{
    min-width:100%;
    height:150px;
    display:grid;
    place-items:center;
    position:relative;
  }

  .project-images h3{
    position:absolute; 
  }
  .project-images img{
    width: 100%;    
    height:100%;
    object-fit:cover;
  }
  
  input, textarea{
    background-color:  rgb(177, 177, 177);
    border: none;
    box-shadow:  2px 2px 5px rgb(75, 75, 75);
  }
  
  textarea{
    min-width: 50vw;
    min-height: 10rem;
    margin: 1rem;
  }
  
 .edit img{
    width: 5rem;
  }

  
  .edit label{
    padding:0;
    margin-top: 10px;    
  }

.edit {
    -webkit-animation: slideIn 6s ease;
            animation: slideIn 6s ease;
}

.ProsjektDef{
  width: 20vw;
}

@-webkit-keyframes slideIn {
    from{
        -webkit-transform:translateX(100%);
                transform:translateX(100%)
    }
    to{
        -webkit-transform:translateY(0%);
                transform:translateY(0%)
    }
}

@keyframes slideIn {
    from{
        -webkit-transform:translateX(100%);
                transform:translateX(100%)
    }
    to{
        -webkit-transform:translateY(0%);
                transform:translateY(0%)
    }
}

.checkbox{
    display: grid;
    grid-auto-flow: column;
    grid-gap: .1rem;
    gap: .1rem;
    justify-content: start;
    align-items: flex-start;
}
footer {
    width: 100vw;
    height: 20vh;
    background: rgb(0, 19, 19);
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    z-index: 1;
    padding-left: 2rem;
    margin-top: 2rem;
}

footer a{
    text-decoration: none;
    color: rgb(0, 31, 31);
    font-weight: 400;
}

.footertekst{
    color: rgb(255, 162, 36);
    border-width: 1px;
    height: 2px;
    justify-content: center;
    align-items: center;
}

