
main.project-detail{
    padding:0;
    justify-content: start;
    align-items:start;
}

.parallax-overlay{
    background:rgba(0,0,0,.2);
    width:100vw;
    height:100vh;
    display:grid;
    place-items:center;
    color:rgb(0, 0, 0);
    font-size:2rem;
}

.project-detail .project-content{
    min-height:100vh;
}

.project-content > div, h1{
    color: rgb(255, 162, 36);
    place-items: center;
    justify-content: center;
    font-size: 4rem;
}

.project-detail .project-images{
    width:100%;
    display:grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap:.5rem;
}

.project-images img{
    width:100%;
    height:200px;
    object-fit: cover;
}

.pager{
    display:grid;
    grid-auto-flow: column;
    align-items: center;
    justify-content: center;
    gap:4rem;
}

.project .description{
    text-align:left;
}

p, h2{
    font-size: 1.2rem;
    grid-template-columns: 1fr 2fr;
    display: grid;
    margin-left: 10%;
    line-height: 1.5rem;

}

.langfil img{
    width: 100%;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr;

}
